<template>
    <div class="zhuce">
   <div class="zhu">
      <h2>注册</h2>
        <el-input class="in1" 
         placeholder="请输入用户名"
         v-model="userName" type="text" name="userName">
        <i slot="prefix" class="el-input__icon el-icon-user-solid"></i>
    </el-input>
    <div class="err1">{{err1}}</div>
  <el-input class="in2" @keyup.native.enter="yan"  
    placeholder="请输入密码"
     v-model="passWord" type="password" name="passWord">
    <i slot="prefix" class="el-input__icon el-icon-s-goods"></i>
  </el-input>
   <div class="err2">{{err2}}</div>
  <div class="btn" @click="yan">注册</div>
   </div>
  
  

        
        
    </div>
</template>
<script>

export default {
    data(){
        return{
            userName:"",
            passWord:"",
            err1:"",
            err2:"",
           
        }
    },
   
   methods: {
        yan:async function(){
            if(this.userName==""){
                this.err1="用户名不能为空";
                return false
            }
            var patt1 = /^[a-zA-Z0-9_\u4e00-\u9fa5]{6,12}$/
            if(!patt1.test(this.userName)){
                this.err1="用户名只能为六到十二位的汉字、数字、字母、下划线";
                return false
            }
            this.err1=""
             
            if(this.passWord==""){
                this.err2="密码不能为空";
                return false
            }
            var patt2 = /^[a-zA-Z0-9_]{6,12}$/
            if(!patt2.test(this.passWord)){
                this.err2="密码只能为六到十二位数字、字母、下划线";
                return false
            }
             this.err2=""
           


            
            var res = await this.$ajax('/register?userName='+this.userName+'&password='+this.passWord)
          
            if(res.data.code){
                this.$message({
                            type:"success",
                            message:"恭喜您注册成功,即将跳转登录页"
                        })
                setTimeout(() => {
                    this.userName=this.passWord='';
                    this.err1=this.err2=""
                    this.$router.replace('/login')
                }, 2000);
                
            }else{
                this.$message.error('您的昵称已被占用');
                this.userName=this.passWord='';
                this.err1=this.err2=""
                return false
            }
        }
        }
    
}
</script>
<style scoped>
.zhuce{
    width: 450px;
    height: 320px;
    background: #fafafa;
    margin: 0 auto;
    padding: 20px;
   
}
.zhu{
    width: 300px;
    height: 280px;
    margin: 0 auto;
    
    position: relative;
  
}

.zhu h2{
    text-align: center;
   
    color: rgb(26, 212, 26);
    
}
.btn{
    width: 100%;
    height: 40px;
    line-height: 40px;
    text-align: center;
    margin-top: 50px;
    border-radius: 6px;
    background: rgb(26, 212, 26);
    color: white;
   
}
.in1{
    margin-top: 20px
}
.in2{
    margin-top: 50px
}
.err1{
    color: orangered;
    position: absolute;
    top: 96px;
    left: 20px;
    font-size: 14px
   
}
.err2{
    color: orangered;
    position: absolute;
    top: 190px;
    left: 20px;
    font-size: 14px
   
}
    
</style>